<template>
  <v-card class="lb-card" @click="previewToggle();">
    <v-card-title class="stroke--title pa-0 pt-2 pl-2">
      <h2 class="text-xs-left font-weight-medium" >Stroke</h2>
      <h2 class="ml-2 font-weight-medium text-xs-left" v-if="!isPreview" transition="fade-transition">Leaderboard</h2>
      <v-spacer></v-spacer>
      <span v-if="!isPreview" class="text-xs-right pr-2" @click="previewToggle('close')">
        <v-icon color="white">clear</v-icon>
      </span>
    </v-card-title>

    <v-card-text v-if="!loading" class="pa-0">
      <stroke-table :preview="isPreview"/>
    </v-card-text>
  </v-card>
</template>
<script>


import { mapState, mapGetters } from 'vuex'
import StrokeTable from './Table'

export default {
  name: 'index',
  props: ['current'],
  components: {
    StrokeTable
  },
  data () {
    return {
      isPreview: true,
      loading: true,
    }
  },

  computed: {
    ...mapState(['stroke_leaderboard']),
    ...mapGetters(['getStrokeLeaderboard', 'userScorecardIds'])
  },

  methods: {
    previewToggle (e) {
      console.log('ee', e)
      this.isPreview = !this.isPreview
      this.toggleParent(this.$parent)
      this.$el.classList.toggle('open')
      this.toggleSibs()
    },
    toggleParent (parent) {
      parent.$el.style.margin = this.isPreview ? '0 auto' : '0';
      parent.$el.style.width = this.isPreview ? '90%' : '100%';
    },
    toggleSibs () {
      let sibs = document.querySelectorAll("#leaderboard-container div.mint-swipe-item:not(.is-active)")
      sibs.forEach(el => el.style.display = (el.style.display === 'none' ? '' : 'none'))
    }
  },
  watch: {
    current () {
      this.$store.dispatch('LOAD_STROKE_LEADERBOARD', { id: this.current.id })
        .then(response => {
          this.loading = false
        })
    },
    isPreview () {
      this.$emit('event')
    }
  },

  created: function () {
    this.$store.dispatch('LOAD_STROKE_LEADERBOARD', { id: this.current.id })
      .then(response => {
        this.loading = false
      })
  },
}
</script>
<style>
.lb-card {
  border-radius: 20px;
  box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.1);
  transition: opacity 1s ease, box-shadow 1s ease;
}
.lb-card.open {
  border-radius: 0;
  top: 0;
  left: 0;
  z-index: 1000 !important;
  transition: opacity 0.2s ease, box-shadow 0.2s ease;
  height: 100vh;
  overflow: scroll;
}

.stroke--title {
  color: #f1f1f1;
  background-color: #30c9e8;
}
</style>


